* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  width: 100%;
  overflow: hidden;
  background-color: #0d1117;
  color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

button {
  border: none;
  outline: none;
}

.container {
  display: grid;
  width: 100%;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 100vh;
}

.markdown-body {
  padding: 45px;
  overflow: auto;
}

.markdown-body pre {
  overflow: auto;
}

@media (max-width: 767px) {
  .markdown-body {
    padding: 15px;
  }
}

.buttons-container {
  position: fixed;
  right: 1rem;
  bottom: 1rem;

  display: grid;
  grid-template-columns: 40px;
  grid-auto-rows: 40px;
  gap: 10px;
}

.download-btn {
  display: grid;
  place-content: center;

  font-size: 20px;
  background-color: #fafafa;

  border-radius: 1rem;
  transition: transform 0.2s ease;
}

.download-btn:hover {
  transform: scale(1.1);
}

.download-btn:active {
  transform: scale(0.9);
}
